<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  mounted () {
    const url = decodeURI(window.location.href)
    const urlObj = this.getQuery(url)
    if (urlObj.successUrl) {
      window.config.successUrl = urlObj.successUrl
    }

    if (urlObj.failedUrl) {
      window.config.failedUrl = urlObj.failedUrl
    }

    if (urlObj.returnUrl) {
      window.config.returnUrl = urlObj.returnUrl
    }
  },
  methods: {
    getQuery (url) {
      const obj = {}
      if (!url) return obj
      const str = url.split('?')[1]
      if (!str) return obj
      const keys = str.split('&')
      keys.forEach((item, idx, data) => {
        const arr = item.split('=')
        obj[arr[0]] = arr[1]
      })
      return obj
    }
  }
}
</script>

<style lang="less">
* {
  box-sizing: border-box;
}

#app {
  font-size: 30px;
}
</style>
